export const getDocCategoryById = (
  subDocs: DocumentCategoryInterface[],
  docId: string
): DocumentCategoryInterface => {
  return subDocs.filter((doc) => doc.user_detail_category_id === docId)[0];
};

export const getSubDocCategoryById = (
  subDocs: DocumentSubCategoryInterface[],
  docId: string
): DocumentSubCategoryInterface => {
  return subDocs.filter((doc) => doc.user_detail_subcategory_id === docId)[0];
};

