import React from "react";
import { MdCheckBox, MdCheckBoxOutlineBlank } from "react-icons/md";
import { DocumentAttributesInterface } from "../../shared/interfaces";

interface MdCheckBoxProps {
  categoryAttributes: DocumentAttributesInterface[];
  attribute: DocumentAttributesInterface;
  error: {
    element: string;
    msg: string;
  };
  updateCategoryValue: (
    value: DocumentAttributesInterface,
    isAvailable: boolean
  ) => void;
  setError: (
    value: React.SetStateAction<{
      element: string;
      msg: string;
    }>
  ) => void;
}

const CheckBox: React.FC<MdCheckBoxProps> = ({
  categoryAttributes,
  attribute,
  error,
  updateCategoryValue,
  setError,
}) => {
  return (
    <div className="cursor-pointer">
      {categoryAttributes && categoryAttributes.includes(attribute) ? (
        <MdCheckBox
          className={`form-checkbox h-7 w-7 ${
            error.element === "category_attributes"
              ? "text-red-600"
              : "text-gray-600"
          }`}
          onClick={() => {
            updateCategoryValue(
              attribute,
              categoryAttributes.includes(attribute)
            );
            setError({ element: "", msg: "" });
          }}
        />
      ) : (
        <MdCheckBoxOutlineBlank
          className={`form-checkbox h-7 w-7 ${
            error.element === "category_attributes"
              ? "text-red-600"
              : "text-gray-600"
          }`}
          onClick={() => {
            updateCategoryValue(
              attribute,
              categoryAttributes.includes(attribute)
            );
            setError({ element: "", msg: "" });
          }}
        />
      )}
    </div>
  );
};

export default React.memo(CheckBox);
