import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import {
  DocumentCategoryInterface,
  DocumentAttributesInterface,
} from "../../shared/interfaces";
import {
  createDocumentCategory,
  updateDocumentCategory,
} from "../../actions/document";

interface AppProps {
  thisCategory: DocumentCategoryInterface | null;
  categoryTitle: string;
  categoryDescription: string;
  actionType: string;
  loading: boolean;
  error: {
    element: string;
    msg: string;
  };
  setCategoryId: (value: string) => void;
  setCategoryTitle: (value: string) => void;
  setCategoryDescription: (value: string) => void;
  setCategoryAttributes: (value: DocumentAttributesInterface[]) => void;
  setError: React.Dispatch<
    React.SetStateAction<{
      element: string;
      msg: string;
    }>
  >;
}
const CreateStepOne: React.FC<AppProps> = ({
  thisCategory,
  categoryTitle,
  categoryDescription,
  actionType,
  loading,
  error,
  setCategoryId,
  setCategoryTitle,
  setCategoryDescription,
  setCategoryAttributes,
  setError,
}) => {
  const [formLoaded, setFormLoaded] = useState(false);
  useEffect(() => {
    if (thisCategory && actionType === "UPDATE_CAT" && !formLoaded) {
      setCategoryId(thisCategory.user_detail_category_id);
      setCategoryTitle(thisCategory.title);
      setCategoryDescription(thisCategory.description);
      setCategoryAttributes(thisCategory.fields);
      setFormLoaded(true);
    }
  }, [
    thisCategory,
    actionType,
    formLoaded,
    setCategoryId,
    setCategoryTitle,
    setCategoryDescription,
    setCategoryAttributes,
  ]);

  return (
    <div
      className={`text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7`}
    >
      <h1 className="text-3xl font-semibold mt-3 mb-2 text-center">
        Basic Information
      </h1>
      <div className="flex flex-col">
        <label>Title</label>
        <input
          type="text"
          className={`text-left input-white w-20 inline-block border-2 ${
            error.element === "category_title"
              ? "border-red-700"
              : "border-gray-500"
          }`}
          placeholder="Category Title"
          value={categoryTitle}
          onChange={(e) => {
            setCategoryTitle(e.target.value);
            setError({ element: "", msg: "" });
          }}
          disabled={loading}
        />
        {error.element === "category_title" ? (
          <p className="text-red-700 mt-2 text-base animate__animated animate__bounce">
            {error.msg}
          </p>
        ) : null}
      </div>
      <div className="flex flex-col">
        <label>Description</label>
        <textarea
          className={`text-left input-white w-20 inline-block border-2 ${
            error.element === "category_description"
              ? "border-red-700"
              : "border-gray-500"
          }`}
          placeholder="Category Description"
          style={{ resize: "none" }}
          value={categoryDescription}
          onChange={(e) => {
            setCategoryDescription(e.target.value);
            setError({ element: "", msg: "" });
          }}
          rows={2}
          disabled={loading}
        ></textarea>
        {error.element === "category_description" ? (
          <p className="text-red-700 mt-2 text-base animate__animated animate__bounce">
            {error.msg}
          </p>
        ) : null}
      </div>
    </div>
  );
};

export default React.memo(
  connect(null, {
    createDocumentCategory,
    updateDocumentCategory,
  })(CreateStepOne)
);
