import axios from "axios";
import React, { useState } from "react";
import { AiFillDelete } from "react-icons/ai";
import { BiDetail } from "react-icons/bi";
import { MdAdd, MdCreate } from "react-icons/md";
import { connect } from "react-redux";
import { PushPopupAlert } from "../../actions";
import { deleteDocumentSubCategory } from "../../actions/document";
import ActionConfirm, {
  ActionConfirmEnum,
} from "../../components/ActionConfirm/ActionConfirm";
import { PopupAlertData } from "../../components/Alert/Popup";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { isEmptyOrSpaces } from "../../shared/dataTest";
import {
  DocumentCategoryInterface,
  DocumentSubCategoryInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";
import { errorToText } from "../../utils/errors";
import searchData from "../../utils/search";

interface AppProps {
  thisCategory: DocumentCategoryInterface;
  actionType: string;
  deleteDocumentSubCategory: Function;
  documentSubCategory: DocumentSubCategoryInterface[] | null;
  viewFields: "" | "SUB_CATEGORY" | "CATEGORY";
  subCategoryId: string;
  setViewFields: (value: "" | "SUB_CATEGORY" | "CATEGORY") => void;
  setSubCategoryId: (value: string) => void;
  setSubCategoryTitle: (value: string) => void;
  setSubCategoryDescription: (value: string) => void;
  setNullId: (value: string) => void;
  setActionType: (
    value:
      | ""
      | "CREATE_SUB"
      | "CREATE_CAT"
      | "UPDATE_SUB"
      | "UPDATE_CAT"
      | "CREATE_FIELD"
      | "UPDATE_FIELD"
  ) => void;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

const SubCategory: React.FC<AppProps> = ({
  thisCategory,
  actionType,
  viewFields,
  subCategoryId,
  documentSubCategory,
  deleteDocumentSubCategory,
  setViewFields,
  setActionType,
  setSubCategoryId,
  setSubCategoryTitle,
  setSubCategoryDescription,
  PushPopupAlert,
}) => {
  const [loading, setLoadind] = useState<boolean>(false);
  const [searchSubCategory, setSearchSubCategory] = useState<string>("");
  const [deleteAction, setDeleteAction] = useState<string>("");
  const sub_cats = documentSubCategory
    ? documentSubCategory.filter(
        (sub) => sub.category_id === thisCategory.user_detail_category_id
      )
    : [];
  const handleDelete = async (subCategoryId: string) => {
    // return setDeleteAction(false);
    setLoadind(true);
    try {
      const res = await axios.delete<DocumentSubCategoryInterface>(
        `${API.AUTH}/detailsubcategory/${subCategoryId}`
      );
      if (res.data) {
        deleteDocumentSubCategory(subCategoryId);
        PushPopupAlert({
          title: "Sub Category has been deleted successfully.",
          theme: PopupAlertTheme.success,
        });
      }
    } catch (error) {
      console.log({ doc_SUB_category_error: error });
      PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
    setLoadind(false);
  };

  return (
    <div>
      {/* user details about */}
      <div className="flex flex-row justify-between items-center bg-gray-100 py-2">
        <h2 className="text-gray-600 font-extrabold text-2xl px-2">
          Qualifications of {thisCategory.title}
        </h2>
        <button
          type="button"
          className="btn btn-blue flex items-center focus:outline-none gap-1"
          onClick={() => setActionType("CREATE_SUB")}
          disabled={actionType === "CREATE_SUB" || actionType === "UPDATE_SUB"}
        >
          <MdAdd className="inline-block mr-1 text-xl font-bold" />
          Qualifications
        </button>
      </div>
      {sub_cats.length ? (
        <div>
          <div className="mt-2">
            <input
              onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                setSearchSubCategory(e.target.value)
              }
              type="text"
              className="input-white h-10 text-sm border-2 border-gray-400 focus:border-gray-500"
              placeholder={`Search type of ${thisCategory.title} by title or description`}
              value={searchSubCategory}
            />
          </div>
          {searchData(sub_cats, searchSubCategory).map((sub_category, i) => (
            <div
              key={i}
              className="flex justify-between items-center p-2 my-3 rounded border group hover:bg-gray-50"
            >
              <div className="flex items-center">
                <div className="ml-2">
                  <div className="text-xl font-semibold text-gray-600">
                    {sub_category.title}
                  </div>
                  <div className="text-sm font-light text-gray-500">
                    {sub_category.description}
                  </div>
                </div>
              </div>
              <div className="flex items-center gap-2">
                <button
                  title="Update Sub-Category"
                  className="bg-gray-50 group-hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none"
                  onClick={() => {
                    setSubCategoryId(sub_category.user_detail_subcategory_id);
                    setSubCategoryTitle(sub_category.title);
                    setSubCategoryDescription(sub_category.description);
                    setActionType("UPDATE_SUB");
                  }}
                >
                  <MdCreate className="text-3xl text-blue-700" />
                </button>
                <button
                  title="Delete Sub-Category"
                  className="bg-gray-50 group-hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none"
                  onClick={() =>
                    setDeleteAction(sub_category.user_detail_subcategory_id)
                  }
                  disabled={loading}
                >
                  <AiFillDelete className="text-3xl text-red-700" />
                </button>
              </div>
            </div>
          ))}
        </div>
      ) : (
        <div className="text-center text-3xl text-gray-500 font-medium py-8">
          <BiDetail className="w-40 h-40 mx-auto animate__pulse" />
          No Qualifications Available
        </div>
      )}

      {!isEmptyOrSpaces(deleteAction) && (
        <ActionConfirm
          modalType={ActionConfirmEnum.DELETE}
          confirmFun={() => handleDelete(deleteAction)}
          setModalAction={(status: boolean) => !status && setDeleteAction("")}
          description={
            "Do you really want to delete this qualification? This process can affect positions, candidate documents & application"
          }
        />
      )}
    </div>
  );
};

export default React.memo(
  connect(null, { deleteDocumentSubCategory, PushPopupAlert })(SubCategory)
);
