import axios from "axios";
import React, { useState } from "react";
import { connect } from "react-redux";
import {
  DocumentCategoryInterface,
  DocumentSubCategoryInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";
import {
  createDocumentSubCategory,
  updateDocumentSubCategory,
} from "../../actions/document";
import { PushPopupAlert } from "../../actions";
import { PopupAlertData } from "../../components/Alert/Popup";
import { PopupAlertTheme } from "../../interfaces/popupAlert";

interface AppProps {
  setThisCategory: (value: DocumentCategoryInterface | null) => void;
  thisCategory: DocumentCategoryInterface;
  setSubCategoryId: (value: string) => void;
  subCategoryId: string;
  setSubCategoryTitle: (value: string) => void;
  subCategoryTitle: string;
  setSubCategoryDescription: (value: string) => void;
  subCategoryDescription: string;
  setActionType: (
    value:
      | ""
      | "CREATE_SUB"
      | "CREATE_CAT"
      | "UPDATE_SUB"
      | "UPDATE_CAT"
      | "CREATE_FIELD"
      | "UPDATE_FIELD"
  ) => void;
  actionType: string;
  documentCategory: DocumentCategoryInterface[];
  createDocumentSubCategory: Function;
  updateDocumentSubCategory: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}
const CreateSubDocument: React.FC<AppProps> = ({
  setThisCategory,
  thisCategory,
  setActionType,
  setSubCategoryId,
  subCategoryId,
  setSubCategoryTitle,
  subCategoryTitle,
  setSubCategoryDescription,
  subCategoryDescription,
  actionType,
  documentCategory,
  createDocumentSubCategory,
  updateDocumentSubCategory,
  PushPopupAlert,
}) => {
  const [loading, setLoadind] = useState<boolean>(false);
  const [categoryId, setCategoryId] = useState<string>(
    thisCategory.user_detail_category_id
  );
  const [formError, setFormError] = useState<{
    element: string;
    msg: string;
  } | null>(null);

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setLoadind(true);
    try {
      if (actionType === "CREATE_SUB") {
        const res = await axios.post<DocumentSubCategoryInterface>(
          `${API.AUTH}/detailsubcategory`,
          {
            category_id: thisCategory.user_detail_category_id,
            title: subCategoryTitle,
            description: subCategoryDescription,
          }
        );
        createDocumentSubCategory(res.data);
        PushPopupAlert({
          title: "Sub Category has been created!",
          theme: PopupAlertTheme.success,
        });
        setLoadind(false);
        setSubCategoryTitle("");
        setSubCategoryDescription("");
      } else {
        const formData: DocumentSubCategoryInterface = {
          user_detail_subcategory_id: subCategoryId,
          category_id: thisCategory.user_detail_category_id,
          title: subCategoryTitle,
          description: subCategoryDescription,
        };
        const res = await axios.patch<DocumentSubCategoryInterface>(
          `${API.AUTH}/detailsubcategory`,
          formData
        );
        if (res.data) {
          updateDocumentSubCategory(formData);
          PushPopupAlert({
            title: "Sub Category has been updated!",
            theme: PopupAlertTheme.success,
          });
          setSubCategoryId("");
          setSubCategoryTitle("");
          setSubCategoryDescription("");
          setActionType("");
          setLoadind(false);
        }
      }
    } catch (error) {
      console.log({ doc_SUB_category_error: error });
      setLoadind(false);
    }
  };

  const changeDistrict = (district_id: string) => {
    setCategoryId(district_id);
    setThisCategory(
      documentCategory.filter(
        (item) => item.user_detail_category_id === district_id
      )[0]
    );
  };

  return (
    <form onSubmit={(e) => handleSubmit(e)}>
      <div className="divide-y divide-gray-200 px-5">
        <div className="py-8 text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7">
          <div className="flex flex-col">
            <label>Category</label>
            <select
              className={`rounded px-2 py-2 w-full bg-white focus:outline-none ${
                formError && formError.element === "doc_category"
                  ? "border-red-500"
                  : "border-gray-500"
              } border-2`}
              onChange={(e: React.ChangeEvent<HTMLSelectElement>) => {
                // setCategoryId(e.target.value);
                changeDistrict(e.target.value);
                formError &&
                  formError.element === "doc_category" &&
                  setFormError(null);
              }}
              value={categoryId}
              disabled={loading}
            >
              <option value="">Document Category</option>
              {documentCategory.map((item, index) => (
                <option
                  key={index + item.user_detail_category_id}
                  value={item.user_detail_category_id}
                >
                  {item.title}
                </option>
              ))}
            </select>
            {formError && formError.element === "doc_category" && (
              <span className="text-red-500">{formError.msg}</span>
            )}
          </div>
          <div className="flex flex-col">
            <label>Title</label>
            <input
              type="text"
              className="text-left input-white w-20 inline-block border-gray-500 border-2"
              placeholder="Qualification Title"
              value={subCategoryTitle}
              onChange={(e) => setSubCategoryTitle(e.target.value)}
              required
              disabled={loading}
            />
          </div>
          <div className="flex flex-col">
            <label>Description</label>
            <textarea
              className="text-left input-white w-20 inline-block border-gray-500 border-2"
              placeholder="Qualification Description"
              style={{ resize: "none" }}
              value={subCategoryDescription}
              onChange={(e) => setSubCategoryDescription(e.target.value)}
              rows={2}
              required
              disabled={loading}
            ></textarea>
          </div>
        </div>
        <div className="pt-4 flex justify-between">
          <button
            onClick={() => {
              setActionType("");
              setSubCategoryId("");
              setSubCategoryTitle("");
              setSubCategoryDescription("");
            }}
            className="btn bg-gray-400 text-white hover:bg-gray-600"
            type="button"
            disabled={loading}
          >
            Exit
          </button>
          <button
            className="btn bg-primary text-white hover:bg-blue-600"
            type="submit"
            disabled={loading}
          >
            {loading
              ? "Loading..."
              : actionType === "CREATE_SUB"
              ? "Create"
              : "Save Change"}
          </button>
        </div>
      </div>
    </form>
  );
};

export default React.memo(
  connect(null, {
    createDocumentSubCategory,
    updateDocumentSubCategory,
    PushPopupAlert,
  })(CreateSubDocument)
);
