import React, { Fragment, useState } from "react";
import { connect } from "react-redux";
import { MdAdd, MdCreate, MdDelete } from "react-icons/md";
import { DocumentFieldsInterface } from "../../shared/interfaces";
import { BiDetail } from "react-icons/bi";
import axios from "axios";
import { API } from "../../utils/api";
import { deleteDocumentFields } from "../../actions/document";
import { PushPopupAlert } from "../../actions/system";
import { PopupAlertData } from "../../components/Alert/Popup";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { errorToText } from "../../utils/errors";
import ActionConfirm, {
  ActionConfirmEnum,
} from "../../components/ActionConfirm/ActionConfirm";
import { isEmptyOrSpaces } from "../../shared/dataTest";

interface AppProps {
  categoryId: string;
  thisTitle: string;
  actionType: string;
  documentFields: DocumentFieldsInterface[] | null;
  setDocFieldsId: (value: string) => void;
  setDocFieldsTitle: (value: string) => void;
  setDocFieldsDescription: (value: string) => void;
  setActionType: (
    value:
      | ""
      | "CREATE_SUB"
      | "CREATE_CAT"
      | "UPDATE_SUB"
      | "UPDATE_CAT"
      | "CREATE_FIELD"
      | "UPDATE_FIELD"
  ) => void;
  deleteDocumentFields: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

const Fields: React.FC<AppProps> = ({
  categoryId,
  thisTitle,
  actionType,
  documentFields,
  setDocFieldsId,
  setDocFieldsTitle,
  setDocFieldsDescription,
  setActionType,
  deleteDocumentFields,
  PushPopupAlert,
}) => {
  const [loading, setLoadind] = useState<boolean>(false);
  const [deleteAction, setDeleteAction] = useState<string>("");
  const [searchField, setSearchField] = useState<string>("");
  const handleDelete = async (fieldId: string) => {
    setLoadind(true);
    try {
      const res = await axios.delete<DocumentFieldsInterface>(
        `${API.AUTH}/doctype/subcatdata/${fieldId}`
      );
      if (res.data) {
        deleteDocumentFields(fieldId);
        PushPopupAlert({
          title: `Category's Field has been deleted successfully!`,
          theme: PopupAlertTheme.success,
        });
        setLoadind(false);
      }
    } catch (error) {
      console.log({ doc_SUB_category_error: error });
      PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
      setLoadind(false);
    }
  };

  const searchData = (
    objList: DocumentFieldsInterface[],
    text: string
  ): DocumentFieldsInterface[] => {
    if (undefined === text || text === "") return objList;
    return objList.filter((product) => {
      let flag;
      // let dataKeys = product;

      for (let prop in product) {
        flag = false;
        if (prop === "title" || prop === "description") {
          flag =
            product[prop].toString().toLowerCase().indexOf(text.toLowerCase()) >
            -1;
        }
        if (flag) break;
      }
      return flag;
    });
  };

  return (
    <Fragment>
      {/* user details about */}
      <div className="flex flex-row justify-between items-center bg-gray-100 py-2">
        <h2 className="text-gray-600 font-extrabold text-2xl px-2">
          Fields {thisTitle.length > 0 ? " of " + thisTitle : ""}
        </h2>
        <button
          type="button"
          className="btn border-2 border-blue-500 bg-blue-500 hover:bg-white
        text-white flex items-center font-bold hover:text-gray-700 focus:outline-none"
          onClick={() => setActionType("CREATE_FIELD")}
          disabled={actionType !== ""}
        >
          <MdAdd className="inline-block mr-1 text-xl font-bold" /> Fields
        </button>
      </div>
      {documentFields &&
      documentFields.filter((data) => data.category_id === categoryId).length >
        0 ? (
        <section>
          {/* SEARCH FORM ================================================== */}
          <div className="mt-2">
            <input
              onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                setSearchField(e.target.value)
              }
              type="text"
              className="input-white h-10 text-sm border-2 border-gray-400 focus:border-gray-500"
              placeholder={`Search field of ${thisTitle} by title or description`}
              value={searchField}
            />
          </div>
          {/* END SEARCH FORM ================================================== */}
          {searchData(
            documentFields.filter((data) => data.category_id === categoryId),
            searchField
          ).map((field, index) => (
            <div
              key={index + field._id}
              className="flex justify-between items-center p-2 my-3 rounded border hover:bg-gray-50 group"
            >
              <div className="flex items-center">
                <div className="ml-2">
                  <div className="text-xl font-semibold text-gray-600">
                    {field.title}
                  </div>
                  <div className="text-sm font-light text-gray-500">
                    {field.description}
                  </div>
                </div>
              </div>

              <div className="flex items-center gap-2">
                <button
                  title="Update Field"
                  className={`bg-gray-50 group-hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none ${
                    loading && "cursor-not-allowed"
                  }`}
                  onClick={() => {
                    setActionType("UPDATE_FIELD");
                    setDocFieldsTitle(field.title);
                    setDocFieldsDescription(field.description);
                    setDocFieldsId(field._id);
                  }}
                >
                  <MdCreate className="text-3xl text-blue-700" />
                </button>
                <button
                  title="Delete Field"
                  className={`bg-gray-50 group-hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none ${
                    loading && "cursor-wait"
                  }`}
                  onClick={() => setDeleteAction(field._id)}
                  disabled={loading}
                >
                  <MdDelete className="text-3xl text-red-700" />
                </button>
              </div>
            </div>
          ))}
        </section>
      ) : (
        <div className="text-center text-4xl text-gray-500 font-medium py-8">
          <BiDetail className="w-40 h-40 mx-auto animate__pulse" />
          No Data Found
        </div>
      )}

      {!isEmptyOrSpaces(deleteAction) && (
        <ActionConfirm
          modalType={ActionConfirmEnum.DELETE}
          confirmFun={() => handleDelete(deleteAction)}
          setModalAction={(status: boolean) => !status && setDeleteAction("")}
          description={
            "Do you really want to delete this field? This process can affect positions, candidate documents & application"
          }
        />
      )}
    </Fragment>
  );
};

export default React.memo(
  connect(null, { deleteDocumentFields, PushPopupAlert })(Fields)
);
