import { FiAlertCircle } from "react-icons/fi";
import { GoAlert } from "react-icons/go";
import { MdDeleteForever, MdRestoreFromTrash } from "react-icons/md";

export enum ActionConfirmEnum {
  RESTORE_DELETE = "restore_delete",
  DELETE = "delete",
  INFO = "info",
  OTHER = "other",
}

interface ActionConfirmProps {
  confirmFun: Function;
  setModalAction: (value: boolean) => void;
  description: string;
  modalType: ActionConfirmEnum;
}

const ActionConfirm: React.FC<ActionConfirmProps> = (props) => {
  return (
    <div
      className="min-w-screen h-screen animated fadeIn faster  fixed  left-0 top-0 flex justify-center items-center inset-0 z-50 outline-none focus:outline-none bg-no-repeat bg-center bg-cover"
      id="modal-id"
    >
      <div className="absolute bg-black opacity-80 inset-0 z-0"></div>
      <div className="w-full  max-w-lg p-5 relative mx-auto my-auto rounded-xl shadow-lg bg-white animate__animated animate__zoomIn animate__faster">
        {/* <!--content--> */}
        <div className="">
          {/* <!--body--> */}
          <div className="text-center p-5 flex-auto justify-center">
            {props.modalType === ActionConfirmEnum.DELETE ||
              (props.modalType === ActionConfirmEnum.RESTORE_DELETE && (
                <FiAlertCircle className="w-4 h-4 -m-1 flex items-center text-red-500 mx-auto" />
              ))}
            {props.modalType === ActionConfirmEnum.DELETE ? (
              <MdDeleteForever className="w-16 h-16 flex items-center text-red-500 mx-auto" />
            ) : props.modalType === ActionConfirmEnum.RESTORE_DELETE ? (
              <MdRestoreFromTrash className="w-16 h-16 flex items-center text-yellow-800 mx-auto" />
            ) : props.modalType === ActionConfirmEnum.INFO ? (
              <GoAlert className="w-16 h-16 flex items-center text-blue-500 mx-auto" />
            ) : (
              <GoAlert className="w-16 h-16 flex items-center text-yellow-500 mx-auto" />
            )}
            <h2 className="text-xl font-bold py-4 ">Are you sure?</h2>
            <p className="text-sm text-gray-500 px-8">{props.description}</p>
          </div>
          {/* <!--footer--> */}
          <div className="p-3  mt-2 text-center space-x-4 md:block">
            <button
              className="mb-2 md:mb-0 bg-white px-5 py-2 text-sm shadow-sm font-medium tracking-wider border text-gray-600 rounded-full hover:shadow-lg hover:bg-gray-100"
              onClick={() => props.setModalAction(false)}
            >
              Cancel
            </button>
            <button
              className={`mb-2 md:mb-0 border px-5 py-2 text-sm shadow-sm font-medium tracking-wider text-white rounded-full hover:shadow-lg ${
                props.modalType === ActionConfirmEnum.DELETE
                  ? "border-red-500 hover:bg-red-600 bg-red-500"
                  : props.modalType === ActionConfirmEnum.RESTORE_DELETE
                  ? "border-yellow-700 hover:bg-yellow-800 bg-yellow-700"
                  : props.modalType === ActionConfirmEnum.INFO
                  ? "border-blue-500 hover:bg-blue-600 bg-blue-500"
                  : "border-yellow-500 hover:bg-yellow-600 bg-yellow-500"
              }`}
              onClick={() => {
                props.confirmFun();
                props.setModalAction(false);
              }}
            >
              Confirm
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ActionConfirm;
