import React from "react";
import { MdClear, MdCreate } from "react-icons/md";
import { isEmptyOrSpaces } from "../../shared/dataTest";

interface AppProps {
  docFieldsTitle: string;
  docFieldsDescription: string;
  fieldList:
    | {
        category_id: string;
        subcategory_id: string | null;
        title: string;
        description: string;
        others: [];
      }[];
  indexField: number;
  error: {
    element: string;
    msg: string;
  };
  loading: boolean;
  setDocFieldsTitle: (value: string) => void;
  setDocFieldsDescription: (value: string) => void;
  setFieldList: (
    value: {
      category_id: string;
      subcategory_id: string | null;
      title: string;
      description: string;
      others: [];
    }[]
  ) => void;
  setIndexField: React.Dispatch<React.SetStateAction<number>>;
  setError: React.Dispatch<
    React.SetStateAction<{
      element: string;
      msg: string;
    }>
  >;
}
const CreateStepThree: React.FC<AppProps> = ({
  docFieldsTitle,
  docFieldsDescription,
  fieldList,
  indexField,
  error,
  loading,
  setDocFieldsTitle,
  setDocFieldsDescription,
  setFieldList,
  setIndexField,
  setError,
}) => {
  const addNewField = () => {
    console.log(fieldList);
    if (isEmptyOrSpaces(docFieldsTitle)) {
      return setError({
        element: "field_title",
        msg: "Field Title is required",
      });
    }
    if (isEmptyOrSpaces(docFieldsDescription)) {
      return setError({
        element: "field_description",
        msg: "Field description is required",
      });
    }

    if (indexField >= 0) {
      let array: {
        category_id: string;
        subcategory_id: string | null;
        title: string;
        description: string;
        others: [];
      }[] = [...fieldList];
      array[indexField] = {
        category_id: "",
        subcategory_id: "",
        title: docFieldsTitle,
        description: docFieldsDescription,
        others: [],
      };
      setFieldList(array);
    } else {
      setFieldList([
        ...fieldList,
        {
          category_id: "",
          subcategory_id: "",
          title: docFieldsTitle,
          description: docFieldsDescription,
          others: [],
        },
      ]);
    }

    setDocFieldsTitle("");
    setDocFieldsDescription("");
    setIndexField(-1);
  };

  const removeField = (index: number) => {
    let array: {
      category_id: string;
      subcategory_id: string | null;
      title: string;
      description: string;
      others: [];
    }[] = [...fieldList];
    if (index !== -1) {
      array.splice(index, 1);
      setFieldList(array);
    }
  };

  return (
    <div
      className={`text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7 mb-3`}
    >
      <h1 className="text-3xl font-semibold mt-3 mb-2 text-center">
        Category Fields Settings
      </h1>

      <div className="flex flex-col">
        <label>Title</label>
        <input
          type="text"
          className={`text-left input-white w-20 inline-block border-2 ${
            error.element === "field_title"
              ? "border-red-700"
              : "border-gray-500"
          }`}
          placeholder="Document Field Title"
          value={docFieldsTitle}
          onChange={(e) => {
            setDocFieldsTitle(e.target.value);
            setError({ element: "", msg: "" });
          }}
          disabled={loading}
        />
        {error.element === "field_title" ? (
          <p className="text-red-700 mt-2 text-base animate__animated animate__bounce">
            {error.msg}
          </p>
        ) : null}
      </div>
      <div className="flex flex-col">
        <label>Description</label>
        <textarea
          className={`text-left input-white w-20 inline-block border-2 ${
            error.element === "field_description"
              ? "border-red-700"
              : "border-gray-500"
          }`}
          placeholder="Category Field Description"
          style={{ resize: "none" }}
          value={docFieldsDescription}
          onChange={(e) => {
            setDocFieldsDescription(e.target.value);
            setError({ element: "", msg: "" });
          }}
          rows={2}
          disabled={loading}
        ></textarea>
        {error.element === "field_description" ? (
          <p className="text-red-700 mt-2 text-base animate__animated animate__bounce">
            {error.msg}
          </p>
        ) : null}
      </div>
      <div className="flex justify-end gap-2">
        <button
          title="View Fields"
          type="button"
          className="bg-blue-600 hover:bg-blue-700 px-4 py-1 text-gray-100 rounded hover:shadow-md focus:outline-none"
          onClick={() => !loading && addNewField()}
        >
          Add
        </button>
        {indexField >= 0 && (
          <button
            title="View Fields"
            type="button"
            className="bg-gray-600 hover:bg-gray-700 px-4 py-1 text-gray-100 rounded hover:shadow-md focus:outline-none"
            onClick={() =>
              !loading &&
              (setIndexField(-1),
              setDocFieldsTitle(""),
              setDocFieldsDescription(""))
            }
          >
            Cancel
          </button>
        )}
      </div>

      {fieldList && (
        <section>
          {fieldList.sort().map((field, index) => (
            <div
              key={index}
              className={`flex justify-between items-start p-2 my-1 rounded border ${
                index === indexField
                  ? "bg-blue-100"
                  : "border-gray-200 hover:bg-gray-100 cursor-pointer"
              }`}
              onClick={() =>
                !loading &&
                index !== indexField &&
                (setDocFieldsTitle(field.title),
                setDocFieldsDescription(field.description),
                setIndexField(index))
              }
            >
              <div className="flex items-center">
                <div className="ml-2">
                  <div className="text-xl font-semibold text-gray-600">
                    {field.title}
                  </div>
                  <div className="text-sm font-light text-gray-500">
                    {field.description}
                  </div>
                </div>
              </div>
              {index !== indexField && (
                <div className="flex items-center gap-2">
                  <button
                    title="Update Field"
                    className={`bg-blue-500 hover:bg-blue-600 p-2 rounded-full shadow-md flex justify-center items-center focus:outline-none ${
                      loading && "cursor-not-allowed"
                    }`}
                    onClick={() => {
                      setDocFieldsTitle(field.title);
                      setDocFieldsDescription(field.description);
                      setIndexField(index);
                    }}
                    type="button"
                    disabled={loading || index === indexField}
                  >
                    <MdCreate className="text-white w-4 h-4" />
                  </button>
                  <button
                    title="View Fields"
                    className={`bg-red-500 hover:bg-red-600 p-2 rounded-full shadow-md flex justify-center items-center focus:outline-none ${
                      loading && "cursor-wait"
                    }`}
                    type="button"
                    onClick={() => removeField(index)}
                    disabled={loading || index === indexField}
                  >
                    <MdClear className="text-white w-4 h-4" />
                  </button>
                </div>
              )}
            </div>
          ))}
        </section>
      )}
    </div>
  );
};

export default React.memo(CreateStepThree);
