import axios from "axios";
import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import {
  DocumentAttributesInterface,
  DocumentCategoryInterface,
  DocumentFieldsInterface,
  DocumentSubCategoryInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";
import {
  createDocumentFields,
  updateDocumentFields,
} from "../../actions/document";
import { errorToText } from "../../utils/errors";
import { PushPopupAlert } from "../../actions/system";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { PopupAlertData } from "../../components/Alert/Popup";
import { isEmptyOrSpaces } from "../../shared/dataTest";

const url = `${API.AUTH}/doctype`;

interface AppProps {
  thisCategory: DocumentCategoryInterface;
  subCategoryId: string;
  docFieldsId: string;
  docFieldsTitle: string;
  docFieldsDescription: string;
  documentCategory: DocumentCategoryInterface[];
  documentSubCategory: DocumentSubCategoryInterface[];
  viewFields: "" | "SUB_CATEGORY" | "CATEGORY";
  actionType: string;
  setThisCategory: (value: DocumentCategoryInterface | null) => void;
  setDocFieldsId: (value: string) => void;
  setDocFieldsTitle: (value: string) => void;
  setDocFieldsDescription: (value: string) => void;
  setActionType: (
    value:
      | ""
      | "CREATE_SUB"
      | "CREATE_CAT"
      | "UPDATE_SUB"
      | "UPDATE_CAT"
      | "CREATE_FIELD"
      | "UPDATE_FIELD"
  ) => void;
  createDocumentFields: Function;
  updateDocumentFields: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}
const CreateDocumentField: React.FC<AppProps> = (props) => {
  const [loading, setLoadind] = useState<boolean>(false);
  const [categoryId, setCategoryId] = useState<string>("");
  const [subCategoryID, setSubCategoryID] = useState<string>("null");
  const [formError, setFormError] = useState<{
    element: string;
    msg: string;
  } | null>(null);

  useEffect(() => {
    if (!categoryId.length && props.thisCategory) {
      let data: DocumentCategoryInterface[] = props.documentCategory.filter(
        (element) =>
          element.user_detail_category_id ===
          props.thisCategory.user_detail_category_id
      );
      if (data.length > 0) {
        if (
          data[0].fields.includes(
            DocumentAttributesInterface.category_subcategory_data
          )
        ) {
          setCategoryId(props.thisCategory.user_detail_category_id);
        }
      }
    }
    if (!subCategoryID.length && props.subCategoryId) {
      setSubCategoryID(props.subCategoryId);
    }
  }, [
    categoryId.length,
    props.documentCategory,
    props.subCategoryId,
    props.thisCategory,
    subCategoryID.length,
  ]);

  const isFieldRequired = (category_id: string): boolean => {
    let data: DocumentCategoryInterface[] = props.documentCategory.filter(
      (element) => element.user_detail_category_id === category_id
    );
    if (data.length > 0) {
      if (
        data[0].fields.includes(
          DocumentAttributesInterface.category_subcategory_data
        )
      )
        return true;
      else return false;
    } else {
      return false;
    }
  };

  const changeCategoryType = (category: string) => {
    setCategoryId(category);
    props.setThisCategory(
      isEmptyOrSpaces(category)
        ? null
        : props.documentCategory.filter(
            (item) => item.user_detail_category_id === category
          )[0]
    );
  };

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    console.log({
      category_id: categoryId,
      subcategory_id:
        props.viewFields === "SUB_CATEGORY"
          ? subCategoryID === "null"
            ? null
            : subCategoryID
          : subCategoryID,
      title: props.docFieldsTitle,
      description: props.docFieldsDescription,
      others: [],
    });
    if (!categoryId.length || isEmptyOrSpaces(categoryId)) {
      return setFormError({
        element: "doc_category",
        msg: "Please select a category",
      });
    }

    if (
      (!subCategoryID.length || isEmptyOrSpaces(subCategoryID)) &&
      isFieldRequired(categoryId)
    ) {
      return setFormError({
        element: "doc_sub_category",
        msg: "Please select a sub category",
      });
    }

    if (!props.docFieldsTitle.length) {
      return setFormError({
        element: "field_title",
        msg: "Please enter Field Title",
      });
    }

    if (!props.docFieldsDescription.length) {
      return setFormError({
        element: "field_desc",
        msg: "Please enter Field Description",
      });
    }

    setLoadind(true);
    console.log({
      category_id: categoryId,
      subcategory_id: subCategoryID === "null" ? null : subCategoryID,
      title: props.docFieldsTitle,
      description: props.docFieldsDescription,
      others: [],
    });
    try {
      if (props.actionType === "CREATE_FIELD") {
        const res = await axios.post<{
          data: {
            category_subcategory_Data_id: string;
            category_id: string;
            subcategory_id: string | null;
            title: string;
            description: string;
            others: [];
          };
        }>(`${url}/subcatdata`, {
          category_id: props.thisCategory.user_detail_category_id,
          subcategory_id: subCategoryID === "null" ? null : subCategoryID,
          title: props.docFieldsTitle,
          description: props.docFieldsDescription,
          others: [],
        });
        console.log({ res });
        props.createDocumentFields({
          _id: res.data.data.category_subcategory_Data_id,
          category_id: res.data.data.category_id,
          subcategory_id: res.data.data.subcategory_id,
          title: res.data.data.title,
          description: res.data.data.description,
          others: res.data.data.others,
        });
        props.PushPopupAlert({
          title: `${
            props.viewFields === "SUB_CATEGORY" ? "Sub Category" : "Category"
          }'s Field has been created!`,
          theme: PopupAlertTheme.success,
        });
        props.setDocFieldsTitle("");
        props.setDocFieldsDescription("");
      } else if (props.actionType === "UPDATE_FIELD") {
        const formData: {
          category_subcategory_data_id: string;
          category_id: string;
          subcategory_id: string | null;
          title: string;
          description: string;
          others: [];
        } = {
          category_subcategory_data_id: props.docFieldsId,
          category_id: categoryId,
          subcategory_id: subCategoryID === "null" ? null : subCategoryID,
          title: props.docFieldsTitle,
          description: props.docFieldsDescription,
          others: [],
        };
        const reducerData: DocumentFieldsInterface = {
          _id: props.docFieldsId,
          category_id: categoryId,
          subcategory_id: subCategoryID === "null" ? null : subCategoryID,
          title: props.docFieldsTitle,
          description: props.docFieldsDescription,
          others: [],
        };
        const res = await axios.patch<{ data: DocumentFieldsInterface }>(
          `${url}/subcatdata`,
          formData
        );
        if (res.data.data) {
          props.updateDocumentFields(reducerData);
          props.PushPopupAlert({
            title: `${
              props.viewFields === "SUB_CATEGORY" ? "Sub Category" : "Category"
            }'s Field has been updated!`,
            theme: PopupAlertTheme.success,
          });
          props.setDocFieldsId("");
          props.setDocFieldsTitle("");
          props.setDocFieldsDescription("");
          props.setActionType("");
          setLoadind(false);
        }
      }
    } catch (error) {
      console.error({ doc_SUB_category_error: error });
      props.PushPopupAlert({
        title: "Updating Field",
        description: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
    setLoadind(false);
  };

  return (
    <form onSubmit={(e) => handleSubmit(e)}>
      <div className="divide-y divide-gray-200 px-6">
        <div className="my-4 text-base text-gray-700 sm:text-lg">
          <div className="flex flex-col">
            <label>Category</label>
            <select
              className={`rounded px-2 py-2 w-full bg-white focus:outline-none ${
                formError && formError.element === "doc_category"
                  ? "border-red-500"
                  : "border-gray-500"
              } border-2`}
              onChange={(e: React.ChangeEvent<HTMLSelectElement>) => {
                // setCategoryId(e.target.value);
                changeCategoryType(e.target.value);
                setSubCategoryID("null");
                formError &&
                  formError.element === "doc_category" &&
                  setFormError(null);
              }}
              value={categoryId}
              disabled={loading}
            >
              <option value="">Document Category</option>
              {props.documentCategory
                .filter((element) =>
                  element.fields.includes(
                    DocumentAttributesInterface.category_subcategory_data
                  )
                )
                .map((item, index) => (
                  <option
                    key={index + item.user_detail_category_id}
                    value={item.user_detail_category_id}
                  >
                    {item.title}
                  </option>
                ))}
            </select>
            {formError && formError.element === "doc_category" && (
              <span className="text-red-500">{formError.msg}</span>
            )}
          </div>
          {false && (
            <div className="flex flex-col">
              <label>Sub Category</label>
              <select
                className={`rounded px-2 py-2 w-full bg-white focus:outline-none ${
                  formError && formError!.element === "doc_sub_category"
                    ? "border-red-500"
                    : "border-gray-500"
                } border-2`}
                onChange={(e: React.ChangeEvent<HTMLSelectElement>) => {
                  setSubCategoryID(e.target.value);
                  formError &&
                    formError.element === "doc_sub_category" &&
                    setFormError(null);
                }}
                disabled={loading}
                value={subCategoryID}
              >
                <option value="">Document Sub Category</option>
                {isFieldRequired(categoryId) && (
                  <option value="null">All</option>
                )}
                {props.documentSubCategory
                  .filter((element) => element.category_id === categoryId)
                  .map((item, index) => (
                    <option
                      key={index + item.user_detail_subcategory_id}
                      value={item.user_detail_subcategory_id}
                    >
                      {item.title}
                    </option>
                  ))}
              </select>
              {formError && formError!.element === "doc_sub_category" && (
                <span className="text-red-500">{formError!.msg}</span>
              )}
            </div>
          )}
          <div className="flex flex-col">
            <label>Title</label>
            <input
              type="text"
              className={`text-left input-white w-20 inline-block ${
                formError && formError.element === "field_title"
                  ? "border-red-500"
                  : "border-gray-500"
              } border-2`}
              placeholder="Field Title"
              value={props.docFieldsTitle}
              onChange={(e) => {
                props.setDocFieldsTitle(e.target.value);
                formError &&
                  formError.element === "field_title" &&
                  setFormError(null);
              }}
              disabled={loading}
            />
            {formError && formError.element === "field_title" && (
              <span className="text-red-500">{formError.msg}</span>
            )}
          </div>
          <div className="flex flex-col">
            <label>Description</label>
            <textarea
              className={`text-left input-white w-20 inline-block ${
                formError && formError.element === "field_desc"
                  ? "border-red-500"
                  : "border-gray-500"
              } border-2`}
              placeholder="Field Description"
              style={{ resize: "none" }}
              value={props.docFieldsDescription}
              onChange={(e) => {
                props.setDocFieldsDescription(e.target.value);
                formError &&
                  formError.element === "field_desc" &&
                  setFormError(null);
              }}
              rows={2}
              disabled={loading}
            ></textarea>
            {formError && formError.element === "field_desc" && (
              <span className="text-red-500">{formError.msg}</span>
            )}
          </div>
        </div>
        <div className="pt-4 flex justify-between">
          <button
            onClick={() => {
              props.setActionType("");
              props.setDocFieldsId("");
              props.setDocFieldsTitle("");
              props.setDocFieldsDescription("");
            }}
            className={`btn bg-gray-400 text-white hover:bg-gray-600 focus:outline-none ${
              loading && "cursor-not-allowed"
            }`}
            type="button"
            disabled={loading}
          >
            Exit
          </button>
          <button
            className={`btn bg-primary text-white hover:bg-blue-600 focus:outline-none ${
              loading && "cursor-wait"
            }`}
            type="submit"
            disabled={loading}
          >
            {loading
              ? "Loading..."
              : props.actionType === "CREATE_FIELD"
              ? "Create"
              : "Save Change"}
          </button>
        </div>
      </div>
    </form>
  );
};

export default React.memo(
  connect(null, {
    createDocumentFields,
    updateDocumentFields,
    PushPopupAlert,
  })(CreateDocumentField)
);
