import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import {
  DocumentCategoryInterface,
  DocumentAttributesInterface,
} from "../../shared/interfaces";
import {
  createDocumentCategory,
  updateDocumentCategory,
} from "../../actions/document";
import CheckBox from "./CheckBox";
import { capitalizeFirstLetter } from "../../shared/dataTest";

interface AppProps {
  thisCategory: DocumentCategoryInterface | null;
  actionType: string;
  loading: boolean;
  error: {
    element: string;
    msg: string;
  };
  categoryAttributes: DocumentAttributesInterface[];
  setCategoryId: (value: string) => void;
  setCategoryTitle: (value: string) => void;
  setCategoryDescription: (value: string) => void;
  setCategoryAttributes: (value: DocumentAttributesInterface[]) => void;
  setError: React.Dispatch<
    React.SetStateAction<{
      element: string;
      msg: string;
    }>
  >;
}
const CreateStepTwo: React.FC<AppProps> = ({
  thisCategory,
  categoryAttributes,
  actionType,
  loading,
  error,
  setCategoryId,
  setCategoryTitle,
  setCategoryDescription,
  setCategoryAttributes,
  setError,
}) => {
  const [formLoaded, setFormLoaded] = useState(false);
  useEffect(() => {
    if (thisCategory && actionType === "UPDATE_CAT" && !formLoaded) {
      setCategoryId(thisCategory.user_detail_category_id);
      setCategoryTitle(thisCategory.title);
      setCategoryDescription(thisCategory.description);
      setCategoryAttributes(thisCategory.fields);
      setFormLoaded(true);
    }
  }, [
    thisCategory,
    actionType,
    formLoaded,
    setCategoryId,
    setCategoryTitle,
    setCategoryDescription,
    setCategoryAttributes,
  ]);

  const updateCategoryValue = (
    value: DocumentAttributesInterface,
    isAvailable: boolean
  ) => {
    // Destructure current state array
    const arrayvalue: DocumentAttributesInterface[] = [...categoryAttributes];
    if (isAvailable) {
      var index = arrayvalue.indexOf(value);
      if (index !== -1) {
        arrayvalue.splice(index, 1);
      }
    } else {
      arrayvalue.push(value);
    }
    console.log({ arrayvalue });
    setCategoryAttributes(arrayvalue);
  };

  return (
    <div className="text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7 mb-3">
      <h1 className="text-3xl font-semibold mt-3 mb-2 text-center">
        Attributes settings
      </h1>
      <div className="flex flex-col px-1 md:px-3">
        <label>Attributes</label>
        <span className="text-gray-500 text-sm">
          Required Attributes for this document
        </span>

        <div className="grid grid-flow-row grid-cols-3 grid-rows- gap-4">
          {Object.values(DocumentAttributesInterface).map(
            (itemAttribute, index) => (
              <label
                className="inline-flex items-center mt-3"
                key={Math.random() + index}
              >
                <CheckBox
                  categoryAttributes={categoryAttributes}
                  attribute={itemAttribute}
                  error={error}
                  updateCategoryValue={updateCategoryValue}
                  setError={setError}
                />
                <span className="ml-2 text-gray-700">
                  {itemAttribute ===
                  DocumentAttributesInterface.category_subcategory_data
                    ? "Field"
                    : capitalizeFirstLetter(itemAttribute.replace("_", " "))}
                </span>
              </label>
            )
          )}
        </div>
        {error.element === "category_attributes" ? (
          <p className="text-red-700 mt-2 text-base animate__animated animate__bounce">
            {error.msg}
          </p>
        ) : null}
      </div>
    </div>
  );
};

export default React.memo(
  connect(null, {
    createDocumentCategory,
    updateDocumentCategory,
  })(CreateStepTwo)
);
